import React from 'react';

import drawScene from '../gl-code/scene.js';
import createShaderProgram from '../gl-code/shaders.js';

class FunctionPlot extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      log_scale: 5
    }
  }

  handleWheelEvent(event) {
    const scrollAmount = event.deltaY;
    const delta_log_scale = 0.05;

    var new_log_scale = this.state.log_scale

    if (scrollAmount > 0) {
      new_log_scale -= delta_log_scale;
    } else {
      new_log_scale += delta_log_scale;
    }

    this.setState({log_scale: new_log_scale});
  }

  componentDidUpdate() {
    const canvas = this.refs.canvas;
    canvas.width = canvas.offsetWidth;
    canvas.height = canvas.offsetHeight;

    const gl = canvas.getContext('webgl');

    if (gl === null) {
      console.log('Unable to initialize WebGL.');
      return;
    }

    // Draw scene
    const shaderProgram = createShaderProgram(
      gl,
      this.props.expression,
      Math.exp(this.state.log_scale)
    );
    if (shaderProgram != null) {
      this.props.onError(false);
      drawScene(gl, shaderProgram);
    } else {
      console.log(
	`Expression ${this.props.expression} could not be compiled!`
      );
      this.props.onError(true);
    }
  }

  render() {
    return (
      <div id='function-plot'>
	<canvas
	  ref='canvas'
	  onWheel={(event) => this.handleWheelEvent(event)}
	/>
      </div>
    );
  }
}

export default FunctionPlot;



