import React from 'react';
import './App.css';

import {MuiThemeProvider} from '@material-ui/core/styles';
import theme from './theme.js';

import ControlPanel from './components/ControlPanel.js';
import FunctionPlot from './components/FunctionPlot.js';


class App extends React.PureComponent {
  componentDidMount() {
    this.plotFunction('z');
  }

  constructor(props) {
    super(props);
    this.state = {
      expression: '',
      error: false
    }
  }

  setErrorState(state) {
    if (state !== this.state.error) {
      this.setState({error: state});
    }
  }

  plotFunction(text) {
    this.setState({expression: text.toLowerCase()});
  }

  render() {
    return (
      <MuiThemeProvider theme={theme}>
	<div id='app'>
	  <ControlPanel
	    onChange={(text) => this.plotFunction(text)}
	    error={this.state.error}
	  />
	  <FunctionPlot
	    expression={this.state.expression}
	    onError={(state) => this.setErrorState(state)}
	  />
	</div>
      </MuiThemeProvider>
    );
  }
}

export default App;



